import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
interface Location {
  latitude: number;
  longitude: number;
}
@Injectable({
  providedIn: 'root'
})
export class MapsService {

  constructor(private http: HttpClient) { }

  getLocation () {
    return this.http.get<Location>('https://ipapi.co/json/');
  }

  getSth (lat, lng) {
   return this.http.get('https://maps.googleapis.com/maps/api/place/nearbysearch/json?location=41.885565,-87.62425&rankby=distance&type=restaurant&key=AIzaSyBLhSlB1iIv-Rsdn9tqHgyF2PCuTvsLqNE');
  }
}
