  constructor(private mapService: MapsService, public zone: NgZone) {
  }

  ngOnInit() {
    this.mapService.getLocation().subscribe(data => {
      console.log(data);
      this.lat = data.latitude;
      this.lng = data.longitude;
      this.center = new google.maps.LatLng(this.lat, this.lng);
      this.circle = new google.maps.Circle({
        center: this.center,
        radius: 2000,
      });
    });
    this.map = new google.maps.Map(document.getElementById('map'), {
      center: this.center,
      zoom: 15
    });
    // this.populateMap();
  }

  // populateMap() {
