  nearbySearchDistance(type: string) {
    console.log('how many times');
    this.arrayOfMarkers = [];
    this.updateCenter();
    const service = new google.maps.places.PlacesService(this.map);
    service.nearbySearch({
      location: this.center,
      rankBy: google.maps.places.RankBy.DISTANCE,
      type: type
    }, (result, status, pagination) => {
      this.callback(result, status, pagination);
    });
  }

  nearbySearchTextSearch(type: string) {
    console.log('how many times');
    this.arrayOfMarkers = [];
    this.updateCenter();
    const service = new google.maps.places.PlacesService(this.map);
    service.textSearch({
      query: type,
      location: this.center,
      radius: 3000,
    }, (result, status, pagination) => {
      this.callbackTextSearch(result, status, pagination);
    });
  }

  callback(results, status, pagination) {
    console.log(results.length);
    if (status === google.maps.places.PlacesServiceStatus.OK) {
      // console.log(results.some(r=> this.arrayOfMarkers.indexOf(r) >= 0));
      // console.log(results);
      for (let i = 0; i < results.length; i++) {
        for (let j = 0; j < results[i].types.length; j++) {
          if (this.arrayOfPOI.includes(results[i].types[j])) {
            const d = new google.maps.LatLng(results[i].geometry.location.lat(), results[i].geometry.location.lng());
            const m = <myPoi>{
              place_id: results[i].place_id,
              lat: results[i].geometry.location.lat(),
              lng: results[i].geometry.location.lng(),
              name: results[i].name,
              types: results[i].types,
              distanceFromCenter: this.checkDistance(d)
            };
            this.arrayOfMarkers.push(m);
            // console.log(this.arrayOfMarkers);
          }
        }
      }
      // if (pagination.hasNextPage) {
      //   sleep: 2;
      //   pagination.nextPage();
      // }
      // if (!pagination.hasNextPage) {
      //   this.filterMarkers(this.arrayOfMarkers);
      //   console.log('dupa');
      // }
    }
  }
  callbackTextSearch(results, status, pagination) {
    console.log(results.length);
    if (status === google.maps.places.PlacesServiceStatus.OK) {
      // console.log(results.some(r=> this.arrayOfMarkers.indexOf(r) >= 0));
      // console.log(results);
      for (let i = 0; i < results.length; i++) {
        for (let j = 0; j < results[i].types.length; j++) {
          if (this.arrayOfTextSearchPOI.includes(results[i].types[j])) {
            const d = new google.maps.LatLng(results[i].geometry.location.lat(), results[i].geometry.location.lng());
            const m = <myPoi>{
              place_id: results[i].place_id,
              lat: results[i].geometry.location.lat(),
              lng: results[i].geometry.location.lng(),
              name: results[i].name,
              types: results[i].types,
              distanceFromCenter: this.checkDistance(d)
            };
            this.arrayOfMarkers.push(m);
            // console.log(this.arrayOfMarkers);
          }
        }
      }
      // if (pagination.hasNextPage) {
      //   sleep: 2;
      //   pagination.nextPage();
      // }
      // if (!pagination.hasNextPage) {
      //   this.filterMarkers(this.arrayOfMarkers);
      //   console.log('dupa');
      // }
    }
  }
  checkDistance(marker: google.maps.LatLng) {
    const distance = google.maps.geometry.spherical.computeDistanceBetween(this.center, marker);
    return Math.ceil(distance);
  }


